body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

body {
  background-color: #014747;
  color: white; }

.App {
  text-align: center; }

.App-header {
  background-color: #282c34;
  min-height: 10vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin); }

#timer-label {
  font-size: 4em;
  text-transform: uppercase; }

.pieholder {
  max-width: 390px;
  margin: 20px auto -50px auto; }

.pie {
  background: teal;
  border-radius: 50%;
  -webkit-transform: rotate(-0.25turn);
          transform: rotate(-0.25turn);
  box-shadow: 0px 0px 13px 13px #02eeee; }

path {
  fill: #014747; }

path:hover {
  fill: teal; }

.pie circle {
  stroke: orange;
  stroke-width: 32;
  stroke-dasharray: 100.53088 100;
  fill: none; }

#time-left {
  position: relative;
  top: -240px;
  background-color: teal;
  font-size: 4em;
  font-weight: bold;
  text-shadow: 3px 3px rgba(0, 0, 0, 0.4);
  box-shadow: 0px 0px 10px 10px rgba(0, 0, 0, 0.4);
  border-radius: 50%;
  padding: 47px 10px; }

#adjustments {
  width: 400px;
  margin: 0px auto;
  font-size: 1.3em; }

.plusminus {
  margin: auto 10px; }

.options {
  margin: 20px auto;
  width: 400px; }

#play-sound,
#flash {
  margin: auto 5px; }

input {
  width: 15px;
  height: 15px; }

.button-holder {
  width: 300px;
  font-size: 1.3em; }

button {
  margin: 10px;
  width: 100px;
  height: 40px; }

@media screen and (max-width: 410px) {
  #timer-label {
    font-size: 2em; }
  .pieholder {
    max-width: 250px;
    margin: 20px auto -40px auto; }
  #time-left {
    top: -150px;
    font-size: 2em;
    padding: 30px 10px; }
  #adjustments {
    width: 300px;
    font-size: 0.8em; }
  .button-holder {
    font-size: 1em; } }

